.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #09d3ac;
}

/*NAV*/
.header-heading {
    color: black;
    font-size: 32px;
    text-decoration: none;
    cursor: pointer;
    margin: 8px;
    padding: 8px;
}

.header-heading:hover {
    background-color: lightblue;
    border: 1px solid black;
    border-radius: 8px;
}

.header-container {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: lightgrey;
}

.header-nav {
    justify-content: space-between;
    display: flex;
}

.nav-item {
    /*width: 50%;*/
    margin: 0 15px;
    list-style: none;
    font-size: 24px;
    padding: 8px;
    margin: 8px;
}

.home-container {
    width: 100%;
    min-width: 375px;
    display: flex;
    top: 130px;
    position: relative;
    flex-direction: column;
    margin: 0 32px;
}
.img-nav {
    display: flex;
    margin: auto;
    width: 80%;
    background-color: lightgrey;
    border: 1px solid black;
    border-radius: 8px;
}

.nav-img-container {
    width: 90%;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nav-img {
    background-color: white;
    margin: 16px;
    width: 60%;
    height: auto;
    border: 1px solid black;
    padding: 16px;
    border-radius: 8px;

}

.nav-img:hover {
    background-color: lightblue;
}

.home-heading {
    text-align: center;
    text-decoration: underline;
}

.nav-item:hover {
    background-color: darkgrey;
    border: 1px solid black;
}

/*HYPERTENSION*/

.hypertension-results-container {
    position: relative;
    top: 130px;
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 0 64px;
    align-items: center;
    background-color: lightgrey;
    border: 1px solid black;
    border-radius: 10px;
}


.hypertension-input {
    width: 90%;
    margin: 8px;
    display: flex;
    justify-content: space-between;
}

.classification-input,
.classification,
.date-input,
.sys-bp-input,
.dia-bp-input {
    width: 50%;
}

.summaray-text-area {
    width: 100%;
    height: 100px;
    line-height: 16px;
}

.kidney-text-area {
    width: 100%;
    height: 300px;
    line-height: 16px;
}

.hypertension-classify-button {
    margin: 8px;
    border-radius: 8px;
    border: 1px solid black;
}


.yellow {
    background-color: yellow;
}

.orange {
    background-color: orange;
}

.orange-red {
    background-color: orangered;
}

.red {
    background-color: lightcoral;
}

.black {
    background-color: black;
    color: red;
}

.blue {
    background-color: lightblue;
}

.unknown {
    background-color: pink;
    border: 1px solid yellow;
}


.kidney-input {
    width: 90%;
    margin: 8px;
    display: flex;
    flex-direction: row;
    align-items: start;
    justify-content: space-between;
}

.kidney-results-container {
    position: relative;
    top: 130px;
    display: flex;
    flex-direction: row;
    width: 92%;
    margin: 0 32px;
    /*align-items: center;*/
    background-color: lightgrey;
    border: 1px solid black;
    border-radius: 10px;
    padding: 8px;
}

.kidney-results-inner-container {
    display: flex;
    align-items: start;
    justify-content: start;
    flex-direction: column;
    flex-basis: 50%;
    margin: 8px auto;
    border: 1px solid grey;
    border-radius: 8px;
    padding: 8px;
}

.hr {
    width: 100%;
    height: 2px;
}

.kidney-classify-button {
    margin: 8px;
    border-radius: 8px;
    border: 1px solid black;
    align-self: flex-end;
    /*width: 20%;*/
}

@media only screen and (min-width: 400px) {
    .header-container {
        position: unset;
    }

    .hypertension-results-container {
        top: 8px;
    }

    .kidney-results-container {
        flex-direction: row;
        top: 8px;
    }

    .home-container {
        top: 8px
    }
}
